@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    @apply bg-gray-50 m-0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Container {
    @apply container mx-auto px-4;
}

.Link {
    @apply inline-block text-indigo-500 transition duration-200 ease-linear
    hover:text-indigo-700
}

.Content {
    @apply bg-white shadow-md p-8 rounded-md
}

.Content .Content-Title {
    @apply mb-4 font-bold text-2xl
}

.Status-Online {
    @apply animate-ping absolute inline-flex h-full w-full rounded-full bg-purple-400 opacity-75
}